@import url(//fonts.googleapis.com/earlyaccess/notosanstc.css);
@charset "UTF-8";
html, body {
  margin: 0;
  padding: 0;
  background-color: #44557E;
}
#app {
  font-family: 'Noto Sans TC', '\5FAE\8EDF\6B63\9ED1\9AD4';
}
.postit {
  width: 240px;
  height: 280px;
  position: absolute;
  background-color: transparent;
  color: #E27687;
  display: flex;
}
.postit textarea {
    cursor: default;
    height: 240px;
    max-height: 240px;
    width: 240px;
    border: none;
    background-color: #63A7A4;
    box-shadow: 10px 8px 30px rgba(0, 0, 0, 0.15);
    font-size: 20px;
    resize: none;
    text-align: center;
    overflow: auto;
    font-weight: 500;
    letter-spacing: .1em;
    line-height: 1.3em;
    transition: background-color .5s;
}
.postit textarea:focus {
      outline: none;
}
.postit:hover .controlBar, .postit:focus .controlBar {
    opacity: 1;
}
.controlBar {
  opacity: 0;
  position: absolute;
  bottom: 0px;
  transition: .3s;
}
.controlBar .colorBlock {
    cursor: pointer;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    vertical-align: bottom;
    transition: .3s;
}
.controlBar .delete {
    display: inline-block;
    float: right;
    color: #F4C2CB;
    border: 1px solid #F4C2CB;
    border-radius: 3px;
    font-size: 13px;
    padding: 2px 7px;
    margin-left: 63px;
    cursor: pointer;
    transition: .3s;
}
.controlBar .delete:hover {
      background-color: #F4C2CB;
      color: #44557E;
}
.addButton {
  position: fixed;
  bottom: 40px;
  right: 40px;
  color: #F4C2CB;
  border: 1px solid #F4C2CB;
  font-size: 18px;
  padding: 8px 10px;
  border-radius: 3px;
  cursor: pointer;
  background-color: transparent;
  transition: .3s;
}
.addButton .icon {
    display: inline-block;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #F4C2CB;
    vertical-align: middle;
    margin-right: 10px;
    position: relative;
    bottom: 2px;
}
.addButton .icon:before {
      position: absolute;
      color: #44557E;
      content: '+';
      font-size: 24px;
      left: 48%;
      top: 48%;
      transform: translate(-50%, -50%);
}
.addButton .text {
    display: inline-block;
}
.addButton:hover {
    color: #44557E;
    background-color: rgba(244, 194, 203, 0.8);
    border-color: rgba(244, 194, 203, 0.3);
}
.addButton:hover .icon {
      background-color: #44557E;
}
.addButton:hover .icon:before {
        color: #F4C2CB;
}
.fade-enter-active {
  transition: all .5s ease;
}
.fade-leave-active {
  transition: all .3s ease;
}
.fade-enter, .fade-leave-to {
  transform: scale(0.1);
  opacity: 0;
}
